.about-proj {
  padding-top: 220px;
  padding-bottom: 380px;

  background-image: url("../img/backgrounds/about-bg.png");
  background-position: bottom -120px left 34px;
  background-repeat: no-repeat;
  &__container {
    display: flex;
    align-items: end;
    gap: 221px;
  }

  &__title {
    margin-bottom: 70px;
  }

  &__text {
    & span {
      font-weight: 700;
    }
  }

  &__citation-wrap {
    display: flex;
    flex-direction: column;
    align-items: end;
    
    padding-right: 50px;
    padding-bottom: 20px;
    border-right: 5px solid;
    border-image: linear-gradient(
        228.12deg,
        rgba(254, 163, 57, 0.8) 3.37%,
        rgba(235, 40, 40, 0.8) 24.28%,
        rgba(245, 50, 97, 0.8) 45.2%,
        rgba(245, 45, 237, 0.8) 66.12%
      )
      1;
  }

  &__citation {
    width: 556px;

    color: $side-text-color;
    text-align: right;
    font-size: 34px;
    font-style: italic;
    font-weight: 300;
    letter-spacing: -0.4px;
  }
}

.creator-inf {
  width: 263px;
  margin-top: 40px;

  display: flex;
  align-items: center;
  justify-content: end;
  gap: 30px;

  &__data {
    text-align: right;
  }

  &__name {
    margin-bottom: 2px;

    font-size: 20px;
  }

  &__status {
    color: #aaabbc;
    font-size: 14px;
    font-weight: 400;
  }
}
